@import 'vars';
@import 'mixins';
@import 'blocks/full-width-slider';

.for-bussiness__breadcrumbs {
    margin-left: 10px;
}

.for-bussiness__content {
    .offer {
        .offer__left-block {
            padding: 40px;
            .offer__title {
                font-weight: 700;
                font-size: 26px;
                margin-top: 0;
                text-align: left;
                text-transform: none;
                margin-bottom: 25px;
            }
            .offer-btn {
                margin-top: 40px;
                font-weight: 400;
            }
            .offer__description {
                line-height: 24px;
            }
        }
    }
}

.page-title {
    margin: 35px 0 35px 0;
}