.full-width-slider {
    background-color: map_get($gray, default);
    width: 100%;
    height: 430px;
    .swiper-slide {
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        width: 340px;
    }
    .swiper-pagination {
        position: absolute;
        .swiper-pagination-bullet {
            width: 15px;
            height: 15px;
        }
    }
    .swiper-buttons-container {
        max-width: 1200px;
        position: relative;
        margin: 0 auto;
        top: -52%;
    }
    .full-width-slider__btn-prev,
    .full-width-slider__btn-next {
        width: 42px;
        height: 42px;
        border-radius: 50%;
        background: #ffffff;
        position: absolute;
        z-index: 1;
        cursor: pointer;
        &.swiper-button-disabled {
            cursor: default;
        }
        &:not(.swiper-button-disabled):hover {
            svg path {
                fill: map_get($gray, hover);
            }
        }
        &.swiper-button-disabled {
            opacity: .3;
        }
    }
    &__btn-prev {
        left: 0;
        svg {
            transform: rotate(90deg);
        }
    }
    &__btn-next {
        right: 0;
        svg {
            transform: rotate(-90deg);
        }
    }
    .full-width-slider__btn-pic {
        background-image: url('data:image/svg+xml;base64,PHN2ZyB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciIHdpZHRoPSIxNSIgaGVpZ2h0PSIxMiIgdmlld0JveD0iMCAwIDE1IDEyIj48dGl0bGU+QXJ0Ym9hcmQgNTwvdGl0bGU+PHBhdGggZD0iTTcuNSAxMS43bC03LTguOGExLjUgMS41IDAgMSAxIDIuNC0xLjhsNC42IDUuOCA0LjYtNS44YTEuNSAxLjUgMCAxIDEgMi40IDEuOXoiIGZpbGw9IiMwMDAiLz48L3N2Zz4=');
        display: inline-block;
        width: 19px;
        height: 14px;
        background-size: 0;
        background-repeat: no-repeat;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        svg {
            display: block;
            width: 100%;
            height: 100%;
            path {
                fill: map_get($gray, default);
                transition: fill 300ms;
                width: 100%;
                height: 100%;
            }
        }
    }
}